.l-blogMain {
  background-color: #fff;
  font-size: 16px;
  @include media(tab){
    width: 100%;
    overflow: hidden;
  }
  a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
  }
}
.author-area {
  font-size: 12px;
  .right {
    @include media(374px){
      margin-left: 100px;
    }
  }
}
.l-blogMain * {
  font-family: 'Noto Sans JP';
}
.l-blogHeader {
  width: 100%;
  margin: 0 auto;
  padding: 2% 4%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #F0980E;
}
.blogTitle span {
  // line-height: 1;
  // @include media(tab){
  //   position: relative;
  //   top: 5px;
  // }
  padding: 0 4%;
  margin: 0 auto;
  font-size: 30px;
  letter-spacing: 2px;
  font-family: "游明朝体","ヒラギノ明朝 Pro W3", "Hiragino Mincho Pro","ＭＳ Ｐ明朝", "MS PMincho", serif;
  color: #fff;
  font-weight: normal;
}
.blogSearch {
  max-width: 480px;
  width: 50%;
  padding-right: 20px;
  >form {
    position: relative;
    width: 100%;
    height: 32px;
  }
  input.search {
    width: 100%;
    border-radius: 0;
    border: none;
    padding: 0 10px;
    height: 32px;
    border-radius: 0;
  }
  &__button {
    position: absolute;
    right: 10px;
    top: 5px;
    width: 16px;
    height: 16px;
    border: none;
    background: transparent;
  }
}
.l-blogCategory{
  width: 100%;
  border: solid 1px #E5E5E5;
  @include media(tab){
    overflow: scroll;
    // -ms-overflow-style: none;    /* IE, Edge 対応 */
    // scrollbar-width: none;       /* Firefox 対応 */
    // &::-webkit-scrollbar {  /* Chrome, Safari 対応 */
    //   display:none;
    // }
  }
}
.blogCategory {
  &__list {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 550px;
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 15px;
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 50px;
      text-decoration: none;
      border-top: solid 4px transparent;
      border-bottom: solid 4px transparent;
      color: #000;
      >span {
        display: block;
        position: relative;
      }
    }
    &::before {
      content: "";
      display: inline-block;
      margin-right: 10px;
      width: 12px;
      height: 12px;
      border-radius: 100%;
    }
    &.all::before {
      background-color: $c_all;
    }
    &.ec::before {
      background-color: $c_ec;
    }
    &.global::before {
      background-color: $c_global;
    }
    &.product::before {
      background-color: $c_product;
    }
    &.development::before {
      background-color: $c_development;
    }
    &.other::before {
      background-color: $c_other;
    }
    &.current {
      &::before {
        display: none;
      }
    }
    &:hover,
    &.current{
      a {
        border-bottom-style: solid;
        border-bottom-width: 4px;
        opacity: 1;
      }
      &.all a {
        border-bottom-color: $c_all;
      }
      &.ec a {
        border-bottom-color: $c_ec;
      }
      &.global a {
        border-bottom-color: $c_global;
      }
      &.product a {
        border-bottom-color: $c_product;
      }
      &.development a {
        border-bottom-color: $c_development;
      }
      &.other a {
        border-bottom-color: $c_other;
      }
    }
  }
}
.l-blogBreadcrumbs {
  font-size: 12px;
  background-color: #ede8e2;
  a {
    color: #2c95bc;
  }
  .archive {
    width: auto;
    margin-left: 0;
    float: none;
  }
}
.l-blogContents {
  max-width: 1160px;
  width: auto;
  display: flex;
  justify-content: center;
  margin: 60px auto;
  padding: 0 1.5%;
  @include media(tab){
    flex-direction: column;
    padding: 0 4%;
  }
  &__inner {
    max-width: 860px;
    width: calc(100% - 300px);
    @include media(tab){
      width: 100%;
    }
    .article {
      float: none;
      margin-left: 0;
      >article {
        margin-left: 0;
        max-width: none;
      }
    }
  }
}
.blogList {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -1.5%;
}
.blogItem {
  width: 33.33%;
  padding: 0 1.5%;
  margin-bottom: 45px;
  transition-duration: 0.8s;
  @include media(tab){
    width: 50%;
  }
  a:hover .blogItem__title {
    transition: 0.3s;
    color: #2c95bc;
    text-decoration: underline;
  }
  figure {
    position: relative;
    overflow: hidden;
    padding-bottom: 70.76%;
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      width: auto;
      height: auto;
      min-width: 100%;
      min-height: 100%;
      max-width: 120%;
      transform: translate(-50%,-50%);

    }
  }
  &__info {
    margin: 10px 0;
  }
  &__category {
    display: inline-block;
    height: 21px;
    line-height: 21px;
    padding: 0 5px;
    margin-right: 14px;
    font-size: 12px;
    font-weight: 400;
    color: #fff;
    &.ec {
      background-color: $c_ec;
    }
    &.global {
      background-color: $c_global;
    }
    &.product {
      background-color: $c_product;
    }
    &.development {
      background-color: $c_development;
    }
    &.other {
      background-color: $c_other;
    }
  }
  &__date {
    display: inline-block;
    height: 21px;
    line-height: 21px;
    font-size: 12px;
    font-weight: 500;
    font-family: futura;
    font-weight: 500;
  }
  &__title {
    font-size: 16px;
    font-weight: 500;
  }
}
.pagenavi.blog {
  margin: 60px auto 76px;
}
.l-blogRelated {
  margin-top: 80px;
}
.blogRelated {
  &__head {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 44px;
    @include media(tab){
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 60px;
    }
  }
  &__title {
    font-size: 36px;
    @include media(tab){
      font-size: 28px;
    }
  }
  &__button {
    width: 192px;
    height: 38px;
    border-radius: 4px;
    background-color: #F0980E;
    box-shadow: 0px 2px 0px #CA7D03;
    @include media(tab){
      position: absolute;
      right: 0;
      top: 50px;
    }
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      color: #fff;
      cursor: pointer;
      &:hover {
        text-decoration: none;
      }
    }
  }
  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 -1.5%;
  }
  &__item {
    width: 25%;
    padding: 0 1.5% 45px;
    @include media(tab) {
        width: 50%;
    }
    a:hover {
      .blogRelated__item__title {
        color:#2c95bc;
        text-decoration: underline;
        transition: 0.3s;
      }
    }
    &__figure {
      position: relative;
      padding-bottom: 56%;
      overflow: hidden;
      img {
        width: auto;
        height: auto;
        min-width: 100%;
        min-height: 100%;
        max-width: 120%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
    &__info {
      padding: 10px 0;
    }
    &__category {
      font-size: 14px;
      margin-right: 10px;
      &.ec {
        color: $c_ec;
      }
      &.global {
        color: $c_global;
      }
      &.product {
        color: $c_product;
      }
      &.development {
        color: $c_development;
      }
      &.other {
        color: $c_other;
      }
    }
    &__date {
      font-size: 12px;
    }
    &__title {
      font-size: 14px;
    }
  }
}
.l-blogPagenavi {
  margin: 0 auto;
}
