.l-blogSidenavi {
  width: 240px;
  margin-left: 6%;
  @include media(tab){
    width: 100%;
    margin: 0;
  }
}
.blogSidenavi {
  &__content {
    margin-bottom: 40px;
  }
  &__title {
    font-size: 28px;
    margin-bottom: 25px;
    &.category {
      letter-spacing: -0.07em;
    }
  }
  &__list {
  }
  &__item {
    a {
      margin-bottom: 20px;
      display: flex;
      justify-content: space-between;
      &:hover {
        .blogSidenavi__item__title {
          color:#2c95bc;
          text-decoration: underline;
          transition: 0.3s;
        }
      }
    }
    &__figure {
      width: 90px;
      height: 64px;
      overflow: hidden;
      position: relative;
      .number {
        position: absolute;
        top: 5px;
        left: 5px;
        display: block;
        width: 22px;
        height: 22px;
        line-height: 22px;
        color: #fff;
        text-align: center;
        background-color: #F0980E;
      }
      img {
        max-width: none;
        width: auto;
        height: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }
    }
    &__title {
      width: calc(100% - 105px);
    }
    &:hover img {
    }
    &__title {
      font-size: 14px;
      font-weight: 500;
    }
  }
}
.searchCategory {
  .blogSidenavi__title {
    letter-spacing: -0.2em;
  }
  &__list {
  }
  &__item {
    margin-bottom: 20px;
    a {
      text-decoration: none;
      color: #000;
    }
  }
  &__categoryName {
    position: relative;
    cursor: pointer;
    font-size: 16px;
    font-weight: 400;
    & a:hover {
      color:#2c95bc;
      text-decoration: underline;
      transition: 0.3s;
    }
    &::before {
      content: "";
      display: inline-block;
      margin-right: 10px;
      width: 12px;
      height: 12px;
      border-radius: 100%;
    }
    &::after {
      position: absolute;
      top: 10px;
      right: 0;
      content: "";
      display:block;
      width: 0;
      height: 0;
      transition-duration: 0.3s;
    }
    &.is-open::after {
      border-right: solid 5px transparent;
      border-left: solid 5px transparent;
      border-bottom: solid 5px #999;
    }
    &.is-close::after {
      border-top: solid 5px #999;
      border-right: solid 5px transparent;
      border-left: solid 5px transparent;
    }
    &.ec::before {
      background-color: $c_ec;
    }
    &.global::before {
      background-color: $c_global;
    }
    &.product::before {
      background-color: $c_product;
    }
    &.development::before {
      background-color: $c_development;
    }
    &.other::before {
      background-color: $c_other;
    }
  }
  &__sublist {
    display: none;
  }
  &__subitem {
    padding: 5px 0 0 32px;
    a {
      display: block;
      width: 100%;
      height: 100%;
      &:hover {
        color: #2c95bc;
        text-decoration: underline;
        transition: .3s;
      }
    }
  }
}
.searchMonth {
  &__list {
    position: relative;
    width: 240px;
    height: 44px;
    overflow: hidden;
    border: solid 1px #c0c0c0;
    &::after {
      position: absolute;
      top: 18px;
    	right: 18px;
    	width: 0;
    	height: 0;
    	padding: 0;
    	content: '';
    	border-left: 5px solid transparent;
    	border-right: 5px solid transparent;
    	border-top: 5px solid #999;
    	pointer-events: none;
    }
    select {
      width: 100%;
      height: 100%;
      font-size: 16px;
      border: solid 1px #c4c4c4;
      padding-left: 10px;
      cursor: pointer;
      border: none;
      outline: none;
      box-shadow: none;
      appearance: none;
      padding-left: 1em;
      &::-ms-expand {
        display: none;
      }
    }
  }
}
